import axios from "axios";
import { useEffect, useState } from "react";

const AdminProfileLayer = () => {
  const [profile, setProfile] = useState({
    name: "",
    phone: "",
    email: "",
  });

  const [form, setForm] = useState({
    currentPassword: "",
    newPassword: "",
    confirmPassword: "",
  });
  const [errors, setErrors] = useState("");
  const isStrongPassword = (password) => {
    const strongRegex =
      /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;
    return strongRegex.test(password);
  };
  const adminId = localStorage.getItem("adminId");
  const token = localStorage.getItem("adminToken");

  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_BACKEND_URL}/api/admin/profile/${adminId}`)
      .then((res) => {
        setProfile({
          name: res.data.name,
          phone: res.data.phone,
          email: res.data.email,
        });
      })
      .catch((err) => console.error(err));
  }, []);

  const handleProfileUpdate = async (e) => {
    e.preventDefault();

    try {
      if (!token) {
        alert("Unauthorized. Please log in.");
        return;
      }

      const response = await axios.put(
        `${process.env.REACT_APP_BACKEND_URL}/api/admin/${adminId}/profile`,
        profile,
        {
          headers: {
            Authorization: `Bearer ${token}`,
            "Content-Type": "application/json",
          },
        }
      );

      if (response.status === 200) {
        alert("Profile updated successfully!");
      } else {
        alert("Unexpected response. Please try again.");
      }
    } catch (err) {
      console.error("Profile update error:", err);
      alert("Failed to update profile.");
    }
  };

  const handlePasswordChange = async (e) => {
    e.preventDefault();
    setErrors("");

    if (!isStrongPassword(form.newPassword)) {
      setErrors(
        "Password must be at least 8 characters long and include uppercase, lowercase, number, and special character."
      );
      return;
    }

    if (form.newPassword !== form.confirmPassword) {
      setErrors("New password and confirm password do not match.");
      return;
    }

    try {
      const res = await axios.put(
        `${process.env.REACT_APP_BACKEND_URL}/api/admin/${adminId}/password`,
        {
          currentPassword: form.currentPassword,
          newPassword: form.newPassword,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      if (res.status === 200) {
        alert("Password updated successfully.");
        setForm({ currentPassword: "", newPassword: "", confirmPassword: "" });
      }
    } catch (error) {
      console.error(error);
      setErrors(error.response?.data?.message || "Something went wrong");
    }
  };

  return (
    <>
      <div className="dashboard-body__content profile-content-wrapper z-index-1 position-relative mt-100">
        {/* Profile Content Start */}
        <div className="profile">
          <div className="row gy-4">
            <div className="col-xxl-12 col-xl-12">
              <div className="dashboard-card">
                <div className="dashboard-card__header pb-0">
                  <ul
                    className="nav tab-bordered nav-pills"
                    id="pills-tab"
                    role="tablist"
                  >
                    <li className="nav-item" role="presentation">
                      <button
                        className="nav-link font-18 font-heading active"
                        id="pills-personalInfo-tab"
                        data-bs-toggle="pill"
                        data-bs-target="#pills-personalInfo"
                        type="button"
                        role="tab"
                        aria-controls="pills-personalInfo"
                        aria-selected="true"
                      >
                        Personal Info
                      </button>
                    </li>

                    <li className="nav-item" role="presentation">
                      <button
                        className="nav-link font-18 font-heading"
                        id="pills-changePassword-tab"
                        data-bs-toggle="pill"
                        data-bs-target="#pills-changePassword"
                        type="button"
                        role="tab"
                        aria-controls="pills-changePassword"
                        aria-selected="false"
                      >
                        Change Password
                      </button>
                    </li>
                  </ul>
                </div>
                <div className="profile-info-content">
                  <div className="tab-content" id="pills-tabContent">
                    <div
                      className="tab-pane fade show active"
                      id="pills-personalInfo"
                      role="tabpanel"
                      aria-labelledby="pills-personalInfo-tab"
                      tabIndex={0}
                    >
                      <form onSubmit={handleProfileUpdate} autoComplete="off">
                        <div className="row gy-4">
                          <div className="col-sm-6 col-xs-6">
                            <label
                              htmlFor="fName"
                              className="form-label mb-2 font-18 font-heading fw-600"
                            >
                              Name
                            </label>
                            <input
                              type="text"
                              className="common-input border"
                              id="name"
                              value={profile.name}
                              placeholder="First Name"
                              onChange={(e) =>
                                setProfile({ ...profile, name: e.target.value })
                              }
                            />
                          </div>

                          <div className="col-sm-6 col-xs-6">
                            <label
                              htmlFor="phonee"
                              className="form-label mb-2 font-18 font-heading fw-600"
                            >
                              Phone Number
                            </label>
                            <input
                              type="tel"
                              className="common-input border"
                              id="phone"
                              placeholder="Phone Number"
                              value={profile.phone}
                              onChange={(e) =>
                                setProfile({
                                  ...profile,
                                  phone: e.target.value,
                                })
                              }
                            />
                          </div>
                          <div className="col-sm-6 col-xs-6">
                            <label
                              htmlFor="emailAdddd"
                              className="form-label mb-2 font-18 font-heading fw-600"
                            >
                              Email Address
                            </label>
                            <input
                              type="email"
                              className="common-input border"
                              id="email"
                              placeholder="Email Address"
                              value={profile.email}
                              readOnly
                            />
                          </div>

                          <div className="col-sm-12 text-end">
                            <button className="btn btn-main btn-lg pill mt-4">
                              {" "}
                              Update Profile
                            </button>
                          </div>
                        </div>
                      </form>
                    </div>

                    <div
                      className="tab-pane fade"
                      id="pills-changePassword"
                      role="tabpanel"
                      aria-labelledby="pills-changePassword-tab"
                      tabIndex={0}
                    >
                      <form onSubmit={handlePasswordChange} autoComplete="off">
                        <div className="row gy-4">
                          {errors && (
                            <div className="col-12 text-danger fw-bold">
                              {errors}
                            </div>
                          )}
                          <div className="col-12">
                            <label
                              htmlFor="current-password"
                              className="form-label mb-2 font-18 font-heading fw-600"
                            >
                              Current Password
                            </label>
                            <div className="position-relative">
                              <input
                                type="password"
                                className="common-input common-input--withIcon common-input--withLeftIcon "
                                id="currentPassword"
                                placeholder="************"
                                value={form.currentPassword}
                                onChange={(e) =>
                                  setForm({
                                    ...form,
                                    currentPassword: e.target.value,
                                  })
                                }
                                required
                              />
                              <span className="input-icon input-icon--left">
                                <img
                                  src="assets/images/icons/key-icon.svg"
                                  alt=""
                                />
                              </span>
                              <span
                                className="input-icon password-show-hide fas fa-eye la-eye-slash toggle-password-two"
                                id="#currentPassword"
                              />
                            </div>
                          </div>
                          <div className="col-sm-6 col-xs-6">
                            <label
                              htmlFor="new-password"
                              className="form-label mb-2 font-18 font-heading fw-600"
                            >
                              New Password
                            </label>
                            <div className="position-relative">
                              <input
                                type="password"
                                className="common-input common-input--withIcon common-input--withLeftIcon "
                                id="newPassword"
                                placeholder="************"
                                value={form.newPassword}
                                onChange={(e) =>
                                  setForm({
                                    ...form,
                                    newPassword: e.target.value,
                                  })
                                }
                                required
                              />
                              <span className="input-icon input-icon--left">
                                <img
                                  src="assets/images/icons/lock-two.svg"
                                  alt=""
                                />
                              </span>
                              <span
                                className="input-icon password-show-hide fas fa-eye la-eye-slash toggle-password-two"
                                id="#new-password"
                              />
                            </div>
                          </div>
                          <div className="col-sm-6 col-xs-6">
                            <label
                              htmlFor="confirm-password"
                              className="form-label mb-2 font-18 font-heading fw-600"
                            >
                              Confirm Password
                            </label>
                            <div className="position-relative">
                              <input
                                type="password"
                                className="common-input common-input--withIcon common-input--withLeftIcon "
                                id="confirm-password"
                                placeholder="************"
                                value={form.confirmPassword}
                                onChange={(e) =>
                                  setForm({
                                    ...form,
                                    confirmPassword: e.target.value,
                                  })
                                }
                                required
                              />
                              <span className="input-icon input-icon--left">
                                <img
                                  src="assets/images/icons/lock-two.svg"
                                  alt=""
                                />
                              </span>
                              <span
                                className="input-icon password-show-hide fas fa-eye la-eye-slash toggle-password-two"
                                id="#confirm-password"
                              />
                            </div>
                          </div>
                          <div className="col-sm-12 text-end">
                            <button className="btn btn-main btn-lg pill mt-4">
                              {" "}
                              Update Password
                            </button>
                          </div>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* Profile Content End */}
      </div>
    </>
  );
};

export default AdminProfileLayer;
